import React from 'react';
import PropTypes from 'prop-types';
import {Panel, PanelHeader, HeaderButton, platform, IOS, Group, Div, Cell, Avatar, Button, Link} from '@vkontakte/vkui';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';
import Icon16Verified from '@vkontakte/icons/dist/16/verified';
import './Csn.css'


const osname = platform();

const Ktm = props => (
	<Panel id={props.id}>
		<PanelHeader
			left={<HeaderButton onClick={props.go} data-to="clans">
				{osname === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
			</HeaderButton>}
		>
		    KтM <Icon16Verified />
		</PanelHeader>
		<Group>
			<Div>
        <Cell
          photo="https://pp.userapi.com/c846523/v846523071/202b35/DREU_CaUYew.jpg"
          description="Оффициальная группа"
          bottomContent={<Button  component="a" href="https://vk.com/kotovas_team_klan">Подписаться</Button>}
          before={<Avatar src="https://pp.userapi.com/c846523/v846523071/202b35/DREU_CaUYew.jpg" size={65}/>}
          size="l"
        >
         KтM
        </Cell>
			</Div>
		</Group>
        <Group title="Контакты">
            <Div>
Vk: <Link href="https://vk.com/kotovas_team_klan">@kotovas_team_klan</Link> 
            </Div>
        </Group>
<Group title="Достижения">
              <Div>
              🏆 Победители VersusOps Tournament <br />
🏆 Первое место на Sunscope Tournament S1 <br />
🏆 Первое место на Sunscope Tournament S2 <br />
🏆 Победители VOL Tournament <br />
🏆 Бронзовые призёры MST Pro League <br />
🏆 Третье место на MOBLIVE Invitational S1 <br />
🏆 Третье место на MGL S1 <br />
🏆 Третье место на MGL S2 
              </Div>
            </Group>
            <Group title="Руководство">
            <Cell component="a" href="https://vk.com/ryanerv"
          size="80"
          description="Лидер"
          before={<Avatar src="https://pp.userapi.com/c854528/v854528774/50862/MATkBc8ZKqw.jpg"/>}
        >
         Nate River
        </Cell>
            </Group>
	</Panel>

);

Ktm.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};

export default Ktm;