import React from 'react';
import PropTypes from 'prop-types';
import {Panel, PanelHeader, HeaderButton, platform, IOS, Group, Div, Cell, Avatar, Button, Link} from '@vkontakte/vkui';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';
import './Csn.css'


const osname = platform();

const Ates = props => (
	<Panel id={props.id}>
		<PanelHeader
			left={<HeaderButton onClick={props.go} data-to="clans">
				{osname === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
			</HeaderButton>}
		>
		    AlpTraum eSports
		</PanelHeader>
		<Group>
			<Div>
        <Cell
          photo="https://pp.userapi.com/c850120/v850120945/f1810/t0jJNjAOcy4.jpg"
          description="Оффициальная группа"
          bottomContent={<Button  component="a" href="https://vk.com/ates_alptraum_esports">Подписаться</Button>}
          before={<Avatar src="https://pp.userapi.com/c850120/v850120945/f1810/t0jJNjAOcy4.jpg" size={65}/>}
          size="l"
        >
         AlpTraum eSports
        </Cell>
			</Div>
		</Group>
        <Group title="Контакты">
            <Div>
Vk: <Link href="https://vk.com/ates_alptraum_esports">@ates_alptraum_esports</Link> <br />
Discord: <Link href="https://discord.gg/aySRD3d">discord.gg/aySRD3d</Link> 
            </Div>
        </Group>
<Group title="Достижения">
              <Div>
              🏆 1 место на CIS Open League #4 <br />
              🏆 2 место на турнире Fast Cup <br />
              🏆 2 место на VSLeague VIII <br />
              🏆 2 место на CSN Elite VII <br />
              🏆 3 место на VSLeague XI <br />
              🏆 3 место на Tournament of Crop XI
              </Div>
            </Group>
            <Group title="Руководство">
            <Cell component="a" href="https://vk.com/id239626413"
          size="80"
          description="Лидер"
          before={<Avatar src="https://sun2.beltelecom-by-minsk.userapi.com/c848416/v848416848/ef490/OFSLYAO98pA.jpg"/>}
        >
         Николай Лазарев
        </Cell>
            </Group>
	</Panel>

);

Ates.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};

export default Ates;