import React from 'react';
import PropTypes from 'prop-types';
import {Panel, PanelHeader, HeaderButton, platform, IOS, Group, Div, Cell, Avatar, Button, Link} from '@vkontakte/vkui';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';
import Icon16Verified from '@vkontakte/icons/dist/16/verified';
import './Csn.css'


const osname = platform();

const Yaw = props => (
	<Panel id={props.id}>
		<PanelHeader
			left={<HeaderButton onClick={props.go} data-to="clans">
				{osname === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
			</HeaderButton>}
		>
		    Yarrow <Icon16Verified />
		</PanelHeader>
		<Group>
        <img  className="Csn" src="https://pp.userapi.com/c855728/v855728151/53332/-fCOPPaP4rs.jpg"/>
			<Div>
        <Cell
          photo="https://pp.userapi.com/c855216/v855216151/53e0b/PO61P5xPCtw.jpg"
          description="Оффициальная группа"
          bottomContent={<Button  component="a" href="https://vk.com/yawyarrow">Подписаться</Button>}
          before={<Avatar src="https://pp.userapi.com/c855216/v855216151/53e0b/PO61P5xPCtw.jpg" size={65}/>}
          size="l"
        >
         Yarrow
        </Cell>
			</Div>
		</Group>
        <Group title="Контакты">
            <Div>
Vk: <Link href="https://vk.com/yawyarrow">@yawyarrow</Link> <br />
Discord: <Link href="https://discordapp.com/invite/26wX6Xk">discordapp.com/invite/26wX6Xk</Link> 
            </Div>
        </Group>
<Group title="Достижения">
              <Div>
              🏆 Победа на CIS Open League #2 <br />
🏆 Победа на CIS Open League #4 <br />
🏆 Победа на CIS Open League #5 <br />
              </Div>
            </Group>
            <Group title="Руководство">
            <Cell component="a" href="https://vk.com/id390182026"
          size="80"
          description="Лидер"
          before={<Avatar src="https://pp.userapi.com/c850616/v850616474/12c1c8/slYZDS764WM.jpg"/>}
        >
        Andrei Gulian
        </Cell>
            </Group>
	</Panel>

);

Yaw.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};

export default Yaw;